body{
  background-color: #282c34;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

section.App-main{
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  justify-content: center;
  
  .graphContainer {
    margin: 0 auto;
    margin-bottom: 20px;;
    border: 2px solid #FFF;
    padding: 20px;
    border-radius: 10px;
    min-height: 300px;
    width: 80%;


    .title{
     color: #FFF;
    }
    .chart{
      stroke-width: 0.2;
      .ticks{
        font-size: 5px;
        color: #FFF
      }
    }
    .legend{

      border-radius: 3px;
      margin: 0 auto;
      color:#FFF;
      display: flex;
      flex-direction: row;
      justify-content: center;
      
      .item{
        display: flex;
        flex-direction: row;
        margin-right: 20px;
        .name{
          margin: 0 10px;
        }
        .colorLine{
          margin-top: 6px;
          width: 20px;
          height: 8px;
        }
      }
    }
 }
}

